.myModal{
    position: fixed;
    top:0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background: rgba(0,0,0,0.5);
}
.myModalContent{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding: 25px;
    background: white;
    border-radius: 16px;
    min-width: 250px;
}
.myModal.active{
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}